export default {
  name: 'priceOffer',
  props: {
    userEmail:String
  },
  data: () => ({
    price           :'',            // 가격 제안 금액
    complete        :false,          // 가격 입력창 노출 여부
    disabled        :null,          // 폼 버튼 활성화
    topCloseBtn     :true,          // 상단 닫기 버튼 노출
  }),
  methods: {
    // 버튼 ============================================
    // 모달창 닫기 버튼
    closeBtn(){
      this.$emit('close')
    },
    // 폼 버튼 활성화
    ableBtn(){
      if(this.price >= 5000 && this.price != ''){
        this.disabled = true
      }else{
        this.disabled = null
      }
    },
    // 확인 버튼
    checkForm(){
      if(this.disabled == true){
        this.complete = true
        this.$emit('confirm')
      }
    },
  },
  watch: {
    price(val){
      const rang = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣|a-z]/;
      if(rang.exec(val) !== null) this.price = val.replace(/[^0-9]/g,'');
      if(isNaN(parseFloat(val)))  this.price = '';
    }
  }
}
